body {
    font-family: 'Montserrat', sans-serif;;
    background-color: $main-background-color;
    color: $primary-text-color ;
    font-size: 16px;

}

h1,
h2,
h3,
p {
    margin-top: 0;
}

ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
}

a {
    text-decoration: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

svg {
    display: block;
}

.visually-hidden {

    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}